import React,{Component} from 'react'
import '../assest/css/User.scss'
import Nav from '../common/Nav'
import Step from './Step'
import { Flex, WhiteSpace,Grid,Stepper } from 'antd-mobile';
import {connect} from 'react-redux';
class User extends React.Component {
    state={
      data:{}
    }
    
    render(){
        return (
          <div className="user">
          
            <div className="user-header">
              <Nav  title="我的聚美" mode="dark" />
              <img src="/assest/img/user.png" alt=""/>
              <div className="user-header-btn">
              <button className="gologin">{this.state.data && this.state.data.nikename}</button>
              <span>|</span>
              <button className="goreg" onClick={this.logout}>注销</button>
              </div>
            </div>
            <div className="user-block-title">
              <div className="left">
                <i className="iconfont icon-dingdan"></i>
                <span>我的订单</span>
              </div>
              <div className="right">
                <span>查看所有订单</span>
                <i className="iconfont icon-youjiantou"></i>
              </div>
            </div>
            <div className="user-block-content">
              <div className="grid">
                <i className="iconfont icon-unie64c"></i>
                <span>待付款</span>
              </div>
              <div className="grid">
                <i className="iconfont icon-daishouhuo"></i>
                <span>待收货</span>
              </div>
              <div className="grid">
                <i className="iconfont icon-daipingjia"></i>
                <span>待评价</span>
              </div>
              <div className="grid">
                <i className="iconfont icon-tuihuo"></i>
                <span>退款/退货</span>
              </div>
            </div>
            <div className="user-block-money">
              <i className="iconfont icon-qiandai"></i>
              <span>我的资产</span>
            </div>
            <div className="user-block-activity">
                <span>现金券</span>
                <span>红包</span>
                <span>聚美余额</span>
                <span>礼品卡</span>
            </div>
            <div className="user-block-chat">
              <ul>
                <li>
                  <i className="iconfont icon-shouhoukefu"></i>
                  <span>售后服务</span>
                </li>
                <li>
                  <i className="iconfont icon-yijianfankui"></i>
                  <span>意见反馈</span>
                </li>
                <li>
                  <i className="iconfont icon-shouhuodizhi"></i>
                  <span>收货地址</span>
                </li>
                <li>
                  <i className="iconfont icon-tuichudenglu"></i>
                  <span>退出登录</span>
                </li>
                <li>
                  <i className="iconfont icon-shouhoukefu"></i>
                  <span>400-123-8888</span>
                </li>
              </ul>
            </div>
            </div>
        )
    };
    logout=()=>{
      axios({
        url:'/api/logout'
      }).then(
        res=>{
          // console.log(res);
          if(res.data.err==0){
            console.log(1);
            this.props.history.push("/home")
            this.props.logout({err:1})
            
          }
          
        } 
      )
      

    };
    componentDidMount(){
      axios({
        url:"/api/user",
        method:"post"
      }).then(res=>{
        // console.log(res.);
        this.setState({
          data:res.data.data
        })
      })
    //   axios({
    //     url:"/api/use",
    //     params:{_page:1,_limit:40}
    // }).then(res=>console.log(res))
    }
}

// const mapStateToProps=()=>{};

const mapDispatchToProps=dispatch=>{
  return (
    {logout:(val)=>dispatch({type:"UPDATA_USER",payload:val})}
  )
}

export default connect(null,mapDispatchToProps)(User);
