import React,{Component} from "react";
import { Grid } from 'antd-mobile';

export default class GridList extends React.Component{
    render(){
        let data= [
            {
                icon:"//mp5.jmstatic.com/mobile/other/detail_page_guarantee/interantion_shipping.png?imageView2/2/w/51/q/70",
                text:"海外直供"
            },
            {
                icon:"//mp5.jmstatic.com/mobile/other/detail_page_guarantee/genuine_guarantee.png?imageView2/2/w/51/q/70",
                text:"原装正品"
            },
            {
                icon:"//mp5.jmstatic.com/mobile/other/detail_page_guarantee/global_delivery.png?imageView2/2/w/51/q/70",
                text:"极速到货"
            },
            {
                icon:"//mp5.jmstatic.com/mobile/other/detail_page_guarantee/return_guarantee_7.png?imageView2/2/w/51/q/70",
                text:"轻松退货"
            },

        ]
        return (
            <Grid data={data} hasLine={false}
            onClick={(el,num)=>{console.log(num,el)}}
            renderItem={(dataItem,index) => (
                <div style={{ padding: '12.5px' }} >
                  <img src={dataItem.icon} style={{ width: '3rem', height: '3rem',cursor: "pointer" }} alt="" />
                  <div style={{ color: '#666', fontSize: '14px',marginTop:".3rem",cursor: "pointer"}}>{dataItem.text}
                  </div>
                </div>
              )}/>
        )
    }
}