import React,{Component} from 'react'
import '../assest/css/Login.scss'
import { Button,Flex, WhiteSpace } from 'antd-mobile';

class Login extends React.Component {
  constructor(){
    super()
     this.state={
      username:"",
      password:""
    }
  }
  render(){
      let {username,password} = this.state;
        return (
          <div className="login">
            <Flex>
            <Flex.Item><i className="iconfont icon-zuo1" onClick={()=>this.props.history.go(-1)}></i></Flex.Item>
            <Flex.Item><span>注册</span></Flex.Item>
            <Flex.Item><i className="icon" onClick={()=>{
              // console.log(this.props.history);
              this.props.history.push('/login')
            }}>登录</i></Flex.Item>
            </Flex>
            <div className="login-use">
            <span>用户名密码注册</span>
            </div>
            <div className="login-ipt">
            <div className="login-username">
              <input type="text" name="username" placeholder="请输入用户名" value={username} onChange={this.changeMess}/>
            </div>
            <div className="login-password">
              <input type="password" name="password" placeholder="请输入密码" value={password} onChange={this.changeMess}/>
            </div>
            </div>
            <div className="login-btn">
            <Button type="warning" style={{borderRadius:"20px"}} onClick={()=>this.goReg()}>注册</Button>
            </div>
            <div className="login-agree">
            <span>点击注册,表示同意《聚美优品用户协议》</span>
            </div>
            </div>
        )
    };
    // changeMess = (ev) => {
    //   this.setState({
    //   [ev.target.name]:ev.target.value
    //   })
    // };
    changeMess = (ev) => {
      this.setState({
        [ev.target.name]:ev.target.value
      })
    };
    goReg=()=>{
      console.log(this.state.username,this.state.password);
      axios({
        url:'/api/reg',
        method:"post",
        data:{username:this.state.username,password:this.state.password}
      }).then(
        res=>console.log(res)
      )
    }
}

export default Login;
