import React,{Component} from "react";
import '../assest/css/HomeList.scss'
import {Link} from 'react-router-dom'
import queryString from 'query-string';

class HomeList extends React.Component {

    render(){
        let {list,dataName}=this.props;
        // console.log(list,dataName)
        return (
            <div className="homelist" ref={node => this.contentNode = node}>
            {
                list.map(item=>{
                    return (
                        <div className="homelist-cell" key={item._id}>
                        <Link 
                        to={{pathname:`/detail/${item._id}`,search:queryString.stringify({dataName})}} >
                            <div className="homelist-bck">
                                <img src={item.url} alt=""/>
                            </div>
                            <div className="homelist-cnt">
                                <div className="cnt-top">
                                    <span>{item.des}</span>                    
                                </div>
                                <div className="cnt-wrap">
                                    <span>￥{item.newprice}</span>
                                    <span>¥{item.oldprice}</span>
                                    <span>{item.common}条评论</span>
                                </div>
                            </div>
                        </Link>
                    </div>
                    )
                })
            }
                
            </div>
        )
    };
    componentDidMount(){
        // console.log(this.contentNode);
        this.props.data("子数据")
        // document.documentElement.scrollTop = 1000
        if(this.contentNode){
        // console.log(this.contentNode);
            // this.contentNode.scrollTo(0.1000)
            // console.log(1);
            // ReactDOM.findDOMNode(this.contentNode).scrollTo(0.1000)
            // window.screenTop=1000;
//             setTimeout(()=>{
//                 setTimeout(()=>{
//                     // console.log(1);
//                     setTimeout(()=>{
// document.documentElement.scrollTop = 1000
//                     },0)
                    
//                 },0)
//             },0)
           
            // window.scrollTo(0,1000)
            // this.contentNode.scrollTop = top;
            window.addEventListener('scroll',this.onScrollHandle)
            // window.scrollTo(0,1000)
        }
    };
    onScrollHandle=(event)=>{
        // const clientHeight = event.target.clientHeight
        // const scrollHeight = event.target.scrollHeight
        // const scrollTop = event.target.scrollTop
        // const isBottom = (clientHeight + scrollTop === scrollHeight)
        // console.log('is bottom:' + isBottom)
        // console.log(event.target.scrollTop);
        // console.log(1);
        var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
        // console.log(scrollTop);
    }

}
export default HomeList;