import React,{Component} from 'react'
import '../assest/css/Login.scss'
import { Button,Flex, WhiteSpace } from 'antd-mobile';
import {connect} from 'react-redux'
import asyncAction from "../store/actions";

class Login extends React.Component {
  constructor(){
    super()
     this.state={
      username:"",
      password:""
    }
  }
  render(){
      let {username,password} = this.state;
        return (
          <div className="login">
            <Flex>
            <Flex.Item><i className="iconfont icon-zuo1" onClick={()=>this.props.history.go(-1)}></i></Flex.Item>
            <Flex.Item><span>登录</span></Flex.Item>
            <Flex.Item><i className="icon" onClick={()=>this.props.history.push('/reg')}>注册</i></Flex.Item>
            </Flex>
            <div className="login-use">
            <span >用户名密码登录</span>
            </div>
            <div className="login-ipt">
            <div className="login-username">
              <input type="text" name="username" placeholder="请输入用户名" value={username} onChange={this.changeMess}/>
            </div>
            <div className="login-password">
              <input type="password" name="password" placeholder="请输入密码" value={password} onChange={this.changeMess}/>
            </div>
            </div>
            <div className="login-btn">
            <Button type="warning" style={{borderRadius:"20px"}} onClick={
              ()=>this.props.login({
                username:this.state.username,
                password:this.state.password,
                history:this.props.history,
                _this:this
              })
            }>登录</Button>
            </div>
            <div className="login-agree">
            <span>点击登录,表示同意《聚美优品用户协议》</span>
            </div>
          </div>
        )
    };
    // changeMess = (ev) => {
    //   this.setState({
    //   [ev.target.name]:ev.target.value
    //   })
    // };
    // des=(el)=>{
    //      console.log(1,el);
    // }
    changeMess = (ev) => {
      this.setState({
        [ev.target.name]:ev.target.value
      })
    };
    // subLogin=()=>{
    //   axios({
    //     url:'/api/login',
    //     params:{username:this.state.username,password:this.state.password}
    //   }).then()
    // }
}
const mapStateToProps=state=>{
  return (
    {
      user:state.user
    }
  )
};
const mapDispatchProps=(dispatch,ownProps)=>({
  login:({username,password,history,_this})=>dispatch(asyncAction({
    apiname:'login',
    data:{username,password},
    typeName:'UPDATA_USER',
    method:"post"
  })).then(
    res=>{
      if (res.err===0){
        ownProps.history.push('/user')
      } else {
        alert(res.msg)
      }
      console.log(res);
    }
  )
});

export default connect(
  mapStateToProps,
  mapDispatchProps
)(Login);
