import React,{Component} from 'react'
import '../assest/css/Speed.scss'
import Nav from '../common/Nav'
import Search from './Search'
import HeardNav from '../common/HeardNav'
import GridList from '../common/GridList'
import SpeedList from '../common/SpeedList'

class Speed extends React.Component {
    render(){
        return (
          <div className="speed">
            <Nav title="极速免税店"/>
            <Search/>
            <HeardNav num={1}/>
            <GridList/>
            <div className="speed-top">
              <img src="//f0.jmstatic.com/btstatic/h5/common/icon_title.png" alt=""/>
              <span>今日团购，每天10点上新</span>
            </div>
            <SpeedList/>
          </div>
        )
    }
}

export default Speed;
