import React,{Component} from 'react'
// import name from './HeardNav';
import Search from './Search'
import HeardNav from '../common/HeardNav'
import HomeList from '../common/HomeList'


class Home extends React.Component {
    constructor(){
        super()
        this.state={
            list:[]
        }
    }
    render(){
        let {list} = this.state;
        return (
        <div className="home" >
           <Search />
           <HeardNav num={0}/>
           {list && <HomeList data={this.data} list={list} dataName="home"  ref={node => this.contentNode = node}/>}
        </div>
        )
    };
    data=(data)=>{
        // console.log(data+1);
    }
    onScrollHandle=(event)=>{
        // const clientHeight = event.target.clientHeight
        // const scrollHeight = event.target.scrollHeight
        // const scrollTop = event.target.scrollTop
        // const isBottom = (clientHeight + scrollTop === scrollHeight)
        // console.log('is bottom:' + isBottom)
        // var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
        // console.log(scrollTop);

        // document.documentElement.scrollTop = 1000

    }
    componentDidMount(){
        // console.log(this.contentNode);
        // 这个监听事件，绑在哪个组件身上
        // document.documentElement.scrollTop = 1000
         setTimeout(()=>{
                // document.documentElement.scrollTop = 1000
            },0)
        // if(this.contentNode){
        //     // window.screenTop=1000;
        //     // window.scrollTo(0,1000)
        //     // this.contentNode.scrollTop = 1000;
        //     // console.log(1);
        //     // window.addEventListener('scroll',this.onScrollHandle.bind(this))
        // }
        axios({
            url:"/api/home",
            params:{_page:1,_limit:40}
        }).then(res=>{
            this.setState({
                list:res.data.data
            })
            // document.documentElement.scrollTop = 2000;
        });
        // console.log(window.screenTop())
    }
}

export default Home;
