import React from 'react';
import Loadable from 'react-loadable'
import Search from './Search';
import HeardNav from '../common/HeardNav';
import {Switch,Redirect,Route} from 'react-router-dom'
import Auth from "../guard/Auth";
import Footer from '../common/Footer';
import Home from './Home';
import User from './User';

import Login from './Login'
const Assemble = Loadable({
    loader: () => import('./Assemble'),
    // loading: Loading
    loading:()=>{return null}
});
import ShopCar from './ShopCar';
import Reg from './Reg';
import Detail from './Detail';
import Error from './Error';
// import Assemble from './Assemble';
import Speed from './Speed';
import Special from './Special';
import Mouth from './Mouth';
import Light from './Light';
import Loading from '../common/Loading'
import {connect} from 'react-redux';


class App extends React.Component {
    componentWillReceiveProps(nextprops){
        let path = nextprops.location.pathname;
        // console.log(path);
        this.checkView(path)
    };
    componentDidMount(){
        // console.log(React);
        // console.log(this.axios);
        let path = this.props.location.pathname;
        this.checkView(path)
    };
    checkView=(path)=>{
        if(/home|assemble|user|shopcar/.test(path)){
            this.props.foot(true)
        }
        if(/detail|login|reg/.test(path)){
            this.props.foot(false)
        }
    }
    render(){
        return (
            <div>
            {this.props.bLoad && <Loading/>}
            <Switch>
            <Route path="/home" render={()=>
                <Switch>
                <Route path="/home/speed" component={Speed} />  
                <Route path="/home/special" component={Special} />  
                <Route path="/home/mouth" component={Mouth} />  
                <Route path="/home/light" component={Light} />  
                <Route path="/home" component={Home} />  
                </Switch>
            }/>
            <Route path="/assemble" component={Assemble} />  
            <Auth path="/shopcar" component={ShopCar} />  
            <Auth path="/user" component={User} />  
            <Route path="/login" component={Login} />  
            <Route path="/reg" component={Reg} /> 
            <Route path="/detail/:id" component={Detail} />  
            {/*<Redirect exact from="/" to="/home" />*/}
            <Route path="/" component={Home} />
            <Route component={Error}/>
            </Switch>
            {this.props.bFoot && <Footer/>}

            </div>
        )
    }
}
const mapStateToProps=state=>{
    return ({
        bLoad:state.bLoad,
        bFoot:state.bFoot
    })
};
const mapDisPatchToProps=dispatch=>{
    return({
        foot:(val)=>dispatch({type:"VIEW_FOOTER",payload:val})
    })
}

export default connect(mapStateToProps,mapDisPatchToProps)(App);
