import React,{Component} from 'react'
import '../assest/css/Footer.css'
import { TabBar } from 'antd-mobile';
import {withRouter} from 'react-router-dom';
class Footer extends React.Component {
  state={
    hidden:false,
    selectedTab:'home',
    tabs: [
      {
        id: 1,
        title: '首页',
        key: 'home',
        path: '/home',
        icon: '/assest/img/home.png',
        selectedIcon: '/assest/img/home_active.png'
      },
      {
        id: 2,
        title: '拼团',
        key: 'assemble',
        path: '/assemble',
        icon: '/assest/img/category.png',
        selectedIcon: '/assest/img/category_active.png'
      },
      {
        id: 3,
        title: '购物车',
        key: 'shopcar',
        path: '/shopcar',
        icon: '/assest/img/shop-cart-1.png',
        selectedIcon: '/assest/img/shop-cart-1_active.png'
      },
      {
        id: 4,
        title: '我的',
        key: 'user',
        path: '/user',
        icon: '/assest/img/user.png',
        selectedIcon: '/assest/img/user__active.png'
      }
    ]
  };
  componentWillReceiveProps(nextProps,props){
    // var str= nextProps.location.pathname.split('/')
    var str= nextProps.location.pathname.split('/')[1];
    this.checkRoute(str)
    // console.log(1);
    // console.log(this.props.location.pathname.split('/'));
  }
  componentDidMount(){
    // this.setState=({
    //   selectedTab=
    // })
    console.log(2);
    // console.log( this.props.location.pathname.spilt('/'));
    var str= this.props.location.pathname.split('/')[1];
    this.checkRoute(str)
    //路由观测
  };
  checkRoute=(str)=>{
    this.setState({
      selectedTab:str
    })
  }
    render(){
      let tabs = this.state.tabs
      // console.log(tabs.length);
        return (
          <TabBar
          unselectedTintColor="#888"
          tintColor="#399"
          barTintColor="white"
          hidden={this.state.hidden}
          >
          {
            tabs.map((item,index)=>(
                <TabBar.Item
              title={item.title}
              key={item.key}
              icon={<div style={{
                width: '22px',
                height: '22px',
                background: 'url('+item.icon+') center center /  21px 21px no-repeat'
              }}
              />
              }
              selectedIcon={<div style={{
                width: '22px',
                height: '22px',
                background: 'url('+item.selectedIcon+') center center /  21px 21px no-repeat'
              }}
              />
              }
              //激活的时候，判断是不是等于当前点击的key(设置激活状态)
              //所以在回调里面重写设置state里面的值
              selected={this.state.selectedTab === item.key}
              onPress={()=>this.checkPath(index,item.key)}
            />
              ))
          }
         
          </TabBar>
        )
    }
    checkPath=(index,item)=>{
      this.setState({
      selectedTab: item,
    });
    // this.setState({
    //   selectedTab: key,
    // });
      this.props.history.push(this.state.tabs[index].path)
    }
}

export default withRouter(Footer);
