import React from 'react';
import ReactDom from 'react-dom';

import App from './component/App1';
import {BrowserRouter,Route} from 'react-router-dom'

// import DatePicker from 'antd-mobile/lib/date-picker';  // 加载 JS
// import 'antd-mobile/lib/date-picker/style/css';        // 加载 CSS
import store from './store'
import {Provider} from 'react-redux'

import './plugins/axios'

// let local='';
let local = window.localStorage.getItem('shopId');
if (local){
  // console.log(local)
  let localcnt=JSON.parse(local)
  store.dispatch({type:'FRIST_SHOP',payload:localcnt})
};
let local_user = window.localStorage.getItem('user');
if (local_user){
  // console.log(local)
  // let localcnt=JSON.parse(local_user)
  store.dispatch({type:'FRIST_USER',payload:JSON.parse(local_user)})
}

ReactDom.render(
  <Provider store={store}>
    <BrowserRouter>
      <Route component={App}/>
    </BrowserRouter>
  </Provider>
    ,
    document.querySelector('#root')
  );
    


