import React,{Component} from 'react'
import '../assest/css/HeardNav.css'

import { Tabs, WhiteSpace, Badge } from 'antd-mobile';
import {withRouter} from 'react-router-dom'

class HeardNav extends React.Component {
    render(){
      const tabs = [
        { title: '首页' },
        { title: '极速免税店' },
        { title: '母婴' },
        { title: '轻奢' },
        { title: '名品特卖' },
      ];
        return (
          <div className="heardnav">
          <Tabs
          tabs={tabs}
          swipeable="true"
          initialPage={this.props.num}
          tabBarActiveTextColor="#fe4070"
          tabBarUnderlineStyle={{borderColor:"red"}}
          onTabClick={(tabs,index)=>{this.goTab(tabs,index)}}
          />

          </div>
        )
    }
    goTab=(tabs,index)=>{
      switch(index){
        case 0:
          this.props.history.push("/home");
          break;
        case 1:
          this.props.history.push("/home/speed");
          break;
        case 2:
          this.props.history.push("/home/mouth");
          break;
        case 3:
          this.props.history.push("/home/light");
          break;
        case 4:
          this.props.history.push("/home/special");
          break;
          
      }
    }
}

export default withRouter(HeardNav);
