import React,{Component} from 'react'
import { NavBar, Icon } from 'antd-mobile';
import { Flex, WhiteSpace } from 'antd-mobile';
import {withRouter} from 'react-router-dom'
import '../assest/css/Nav.scss'
class Nav extends React.Component {
    render(){
        return (
          <div className="nav">
          <Flex>
          <Flex.Item ><i className="iconfont icon-zuo1" onClick={()=>this.props.history.go(-1)}></i></Flex.Item>
          <Flex.Item><span >{this.props.title}</span></Flex.Item>
          <Flex.Item><i className="iconfont icon-shouye" onClick={()=>this.props.history.push("home")}></i></Flex.Item>
          </Flex>
          
          </div>
        )
    }
}

export default withRouter(Nav);
