export default ({apiname,params,typeName,method,data})=>(dispatch,getState)=>{
    // dispatch({type:VIEW_LOADING,payload:true});
    // console.log('asyncactions','/mock/'+apiname);
    console.log(apiname,params,typeName);
    return axios({
      url:'/api/'+apiname,
      data:data||null,
      params:params||null,
      method:method||"get"
    }).then(
      res=>{
        console.log(res);
        dispatch({type:typeName,payload:res.data});
        // dispatch({type:VIEW_LOADING,payload:false});
        return {err:res.data.err,msg:res.data.msg};
      }
    );
  }