import React,{Component} from 'react'
import '../assest/css/Detail.scss'
import Nav from '../common/Nav'
import Slide from '../common/Slide'
import queryString from "query-string"
import {connect} from 'react-redux'
import store from '../store'


class Detail extends React.Component {
  constructor(props){
        super(props);
        this.state={
          list:{}
        }
      }
    render(){
      let{list} = this.state
      let detail=list.detail;
        return (
          <div className="detail">
            <Nav title="商品详情"/>
            <div className="detail-show">
              { detail && <Slide list={detail.url}/> } 
            </div>
            {
              detail && <div className="detail-display">
                <div className="detail-price">
                  <div className="left-price">
                    <span>￥{list.newprice}</span>
                    <i>￥{list.oldprice}</i>
                  </div>
                  <div className="right-price">
                    <span>{detail.people}人已购买</span>
                  </div>
                </div>
                <div className="detail-time">
                <span>还剩12天11:33:21</span>
              </div>
              <div className="detail-des">
                <span>{detail.content}</span>
              </div>
              </div> 
            }
            <div className="deatil-city">
              <ul>
                <li>
                <div className="city-address">
                  <span>地址</span>
                </div>
                  <div className="city-des">
                  <span>至<i className="iconfont icon-shouhuodizhi"></i>北京市-市辖区-东城区</span>
                  <i className="iconfont icon-youjiantou"></i>
                  </div>
                </li>
                <li>
                <div className="city-address">
                  <span>运费</span>
                </div>
                  <div className="city-des">
                  <span>件数满1件,全国包邮</span>
                  <i className="iconfont icon-youjiantou"></i>
                  </div>
                </li>
              </ul>
            </div> 
            <div className="deatil-addshop">
            <div className="go-store">
                <i className="iconfont icon-dianpu"></i>
                <span>店铺</span>
            </div>
            <div className="add-store" onClick={()=>{this.goShop()}}>
                <i className="iconfont icon-gouwuche"></i>
                <span>购物车</span>
            </div>
                <span onClick={()=>{this.props.addShop({...list,flag:false})}}>加入购物车</span>
                <span>立即购买</span>
            </div>
          </div>
        )
    }
    componentDidMount(){
      let id=this.props.match.params.id;
      let dataName=queryString.parse(this.props.location.search).dataName;
      axios({
        url:`/api/${dataName}/${id}`
      }).then(res=>{
        this.setState({
          list:res.data.data
        })
      })
    };
    // addShop=()=>{
    //   // console.log(1)
    //   this.add
    // };
    goShop=()=>{
      // console.log(this.props)
      // let local = window.localStorage.getItem('shopId');
      // if (local){
      //   let localcnt=JSON.parse(local)
      //   // console.log(localcnt)
      //   store.dispatch({type:'UPDATE_SHOP',payload:local})
      // }
      // console.log()
      if(this.props.user.err==0){
        this.props.history.push('/shopcar')
      }else{
        if(window.confirm("您还未登录,是否前往登录页面")){
          this.props.history.push('/login')
        } 
       
      }
    }
}
const mapstateToProps=state=>({
  shop:state.shop,
  user:state.user
})

const mapdispatchToProps=dispatch=>({
  addShop:(val)=>dispatch({type:"UPDATE_SHOP",payload:val})
})

export default connect(
  mapstateToProps,
  mapdispatchToProps
)(Detail) ;
